@import url('https://fonts.googleapis.com/css?family=Poppins');
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #141823;
}

.background-main-dark {
  background-color: #141823;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cookie-consent-quote-image {
  margin-right: 15px;
  float: left;
}
.cookie-consent-quote-text {
  /*font-style: italic;*/
  position: relative;
}
.cookie-consent-quote-text::after {
  content: "“";
  font-size: 1.2em;
  position: relative;
  top: 12px;
  color: #eee;
}

.cookie-consent-quote-text::before {
  content: "”";
  font-size: 1.4em;
  position: relative;
  left: -2px;
  color: #eee;
}

.mobile-menu-open {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s;
  z-index: 10;
}

.mobile-menu-open a::before {
  content: "› "
}

.mobile-menu-open.show {
  opacity: 1;
  visibility: visible;
  background: #282c34;
  z-index: 9;
}

.mobile-menu-close-icon {
  z-index: 20;
}
